<template>
    <div class="listwrap">
        <div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item>首页</el-breadcrumb-item>
		        <el-breadcrumb-item>消息管理</el-breadcrumb-item>
                <el-breadcrumb-item>周报推送</el-breadcrumb-item>
	      	</el-breadcrumb>
		</div>
        <div class="listbtn">
            <el-date-picker
                v-model="starttime"
                type="daterange"
                placeholder="选择日期范围"
                :picker-options="pickerDate"
                style="width: 220px">
            </el-date-picker>
			<el-button type="primary" icon="search" @click.native="search">查询</el-button>
		</div>
        <el-popover
          ref="popover4"
          placement="right"
          width="400"
          trigger="click">
          <el-table :data="errorData">
            <el-table-column  property="msg" label="描述"></el-table-column>
            <el-table-column  property="count" label="数量"></el-table-column>
          </el-table>
        </el-popover>
        <div class="listtable">
            <span class="tip"> 共推送 {{ total }} 个，推送成功{{ successcount }}个，失败{{ failcount }}个（<el-button type="text" v-popover:popover4 >点击查看详情</el-button>），成功率: {{ successrat }}%</span>
            <el-table
                v-loading.body="loading"
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                prop="order"
                label="#"
                width="50">
                </el-table-column>
                <el-table-column
                prop="wname"
                label="门店名称">
                </el-table-column>
                <el-table-column
                prop="rname"
                label="用户名">
                </el-table-column>
                <el-table-column
                prop="pushtime"
                label="推送时间">
                </el-table-column>
                <el-table-column
                prop="tag"
                label="状态"
                width="80">
                </el-table-column>
                <el-table-column
                prop="pushresult"
                sortable
                label="推送结果">
                </el-table-column>
                <el-table-column
                inline-template
                label="详情"
                width="120">
                    <span>
                    <el-button @click="detail(row.detail)" type="primary" size="small">查看详情</el-button>
                </span>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
import config from '../model/config'
import utils from '../model/utils'
import format from 'vux/src/components/datetime/format'
export default {
    mounted() {
            var today = new Date();
            today.setHours(0);
            today.setMinutes(0);
            today.setSeconds(0);
            today.setMilliseconds(0);
            var endday = new Date();
            endday.setHours(23);
            endday.setMinutes(59);
            endday.setSeconds(59);
            endday.setMilliseconds(0);
            this.starttime = [today, endday];
            this.showTable(this.starttime[0], this.starttime[1]);
        },
        data() {
            return {
                starttime: [],
                total: 0,
                successcount: 0,
                failcount: 0,
                successrat: 0,
                tableData: [],
                loading: true,
                rData: [],
                errorData: [],
                rdetil: {
                    nouser: 0,
                    wxerror: 0,
                    noopenid: 0,
                    unkownerror: 0
                },
                pickerDate: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                }
            }
        },
        methods: {
            search() {
                this.starttime[0].setHours(0);
                this.starttime[0].setMinutes(0);
                this.starttime[0].setSeconds(0);
                this.starttime[0].setMilliseconds(0);
                this.starttime[1].setHours(23);
                this.starttime[1].setMinutes(59);
                this.starttime[1].setSeconds(59);
                this.starttime[1].setMilliseconds(0);
                if (this.starttime[0] > this.starttime[1]) {
                    this.$message({
                        message: '开始时间不能晚于结束时间，请重新选择时间',
                        type: 'warning'
                    })
                    return;
                }
                // 

                this.showTable(this.starttime[0], this.starttime[1])
            },
            detail(r) {
                this.$notify({
                    title: '提示',
                    message: JSON.stringify(r, null, "\t")
                });
            },
            isEmptyObject(o) {
                for (var x in o) {
                    return false
                }
                return true
            },
            resultdetil() {
                var showstr = {
                    nouser: '用户未加入场馆: ',
                    wxerror: '推送出错: ',
                    onopenid: '没有授权绑定: '
                };
                var message = [];
                var rdetil = this.rdetil;
                for (var i in rdetil) {
                    message.push((showstr[i] || '未知错误：') + rdetil[i]);
                }
                this.$notify({
                    title: '详细',
                    message: message.join('\n')
                });
            },
            showTable(starttime, endtime) {
                this.showtime = format(starttime, "YYYY-MM-DD HH:mm:ss") + ' ~ ' + format(endtime, "YYYY-MM-DD HH:mm:ss");
                var _this = this;

                var url = `${config.rest}/object/get.消息管理后台.weeksportrecords_${starttime.getTime()}_${endtime.getTime()}`
                this.loading = true;
                this.tableData = [];
                this.errorData = [];
                utils.load(url, function(r) {
                    _this.loading = false;
                    var rdata = [];
                    _this.total = 0;
                    _this.successcount = 0;
                    _this.failcount = 0;
                    _this.successrat = 0;
                    _this.rdetil = {
                        nouser: 0,
                        wxerror: 0,
                        noopenid: 0,
                        unkownerror: 0
                    };
                    if (r && r.msgmanage && r.msgmanage.list) {
                        var list = r.msgmanage.list;
                        _this.total = list.length;
                        for (var i in list) {
                            var status = list[i].status;
                            if (status === 'Success') {
                                _this.successcount++;
                            } else {
                                var pr = list[i].pushresult;
                                if (pr.errmsg.indexOf('no openid') > -1) {
                                    _this.rdetil.noopenid += 1;
                                } else if (!list[i].user.id) {
                                    _this.rdetil.nouser += 1;
                                } else if (pr.errmsg.indexOf('access_token is invalid')) {
                                    _this.rdetil.wxerror += 1;
                                } else {
                                    _this.rdetil.unkownerror += 1;
                                }
                            }
                            rdata.push({
                                order: Number(i) + 1,
                                wname: list[i].saas.wname || '',
                                rname: list[i].user.rname || '',
                                tag: list[i].tag,
                                pushtime: format(new Date(list[i].sort), "YYYY-MM-DD HH:mm:ss"),
                                pushresult: status == 'Failed' ? JSON.stringify(list[i].pushresult) : status,
                                detail: list[i].pushresult
                            });
                        }
                    }
                    _this.failcount = _this.total - _this.successcount;
                    if (_this.total > 0) {
                        _this.successrat = (_this.successcount * 100 / _this.total).toFixed(2);
                        var showstr = {
                            nouser: '用户未加入场馆: ',
                            wxerror: '推送出错: ',
                            noopenid: '没有授权绑定: ',
                            unkownerror: '未知错误: '
                        };
                        var message = [];
                        var rdetil = _this.rdetil;
                        for (var i in rdetil) {
                            message.push({
                                msg: showstr[i] || '未知错误: ',
                                count: rdetil[i]
                            });
                        }
                        _this.errorData = message;
                    }
                    _this.tableData = rdata;
                });
            }
        }
}
</script>
<style>
    .listbtn .mark {
        margin: 0 5px;
    }
    .listbtn button {
        margin-left: 5px;
    }
    .listtable .tip {
        font-size: 15px;
        color: #4db3ff;
        border-bottom: 1px solid #4db3ff;
        text-decoration : none
    }
    .el-message-box {
        -webkit-user-select: text;
    }
    .el-message-box__message>p{
        word-break: break-all;
    }
</style>


