import Vue from 'vue'
import config from './config'

export default new Vue({
	methods: {
		postFn(m, p) {
			var f = {
				"method": m,
				"params": [p]
			}
			return JSON.stringify(f);
		},
		rpc(url, m, d, cb) {
			this.$http.post(url, this.postFn(m, d)).then(function(response) {
				if (typeof(response.data) == 'string') {
					response.data = JSON.parse(response.data);
				}
				cb.call(this, response.data);
			}, function(response) {
				cb.call(this, undefined);
			});
		},
		load(url, cb) {
			this.$http.get(url).then(function(response) {
				var data = response.data.substring(7,response.data.length-2)
				data = JSON.parse(data)
				cb.call(this, data);
			}, function(response) {});
		}
	}
});


