<template>
	<div class="listwrap">
		<div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
		        <el-breadcrumb-item :to="{ path: '/' }">客户列表</el-breadcrumb-item>
		        <el-breadcrumb-item>添加商户</el-breadcrumb-item>
	      	</el-breadcrumb>
		</div>
		<div class="shopcon">
	        <el-form :model="form">
	            <el-form-item label="工作室名称" label-width="100px">
	                <el-input v-model="form.gname" auto-complete="off"></el-input>
	            </el-form-item>
	            <el-form-item label="联系电话" label-width="100px">
	                <el-input v-model="form.phone" auto-complete="off"></el-input>
	            </el-form-item>
	            <el-form-item label="等级" label-width="100px">
				<el-select v-model="form.level">
					<el-option
					  v-for="item in level"
					  :label="item.label"
					  :value="item.value">
					</el-option>
				</el-select>
	            </el-form-item>
	        </el-form>
	        <div class="btnfr">
	        	<el-button type="primary" @click.native="save" :loading="saveload"> 保 存 </el-button>
	        </div>
		</div>
	</div>
</template>
<script>
import md5 from '../model/myMd5'
import utils from '../model/utils'
import config from '../model/config'
export default {
	data() {
			return {
				form: {
					phone: '',
					gname: '',
					level: '',
					hpass3: ''
				},
				level: [{
					value: '1',
					label: 'saas平台'
				}, {
					value: '2',
					label: '心率系统16'
				}, {
					value: '3',
					label: '心率系统32'
				}, {
					value: '4',
					label: 'saas+心率系统16'
				}, {
					value: '5',
					label: 'saas+心率系统32'
				}],
				saveload: false
			}
		},
		methods: {
			save: function() {
				var _this = this;
				if (_this.form.phone.trim() == '' && _this.form.gname.trim() == '' && _this.form.level) {
					_this.$alert('请将信息填写完整', '提示');
					return
				} else {
					var reg = new RegExp("^(13[0-9]|14[57]|15[0-9]|17[0678]|18[0-9])\\d{8}$");
					if (!reg.test(_this.form.phone)) {
						_this.$alert('手机号码有误', '提示');
						return
					}
				}
				var hpass3 = md5.hex_hmac(_this.form.phone, md5.hex("123456"));
				_this.form.hpass3 = hpass3;
				_this.saveload = true;
				utils.rpc(config.xhr + '/0/user', 'createsaas', _this.form, function(r) {
					if (r && r.result && !r.result.error) {
						_this.saveload = false;
						_this.$message({
							type: 'success',
							message: '添加成功'
						});
						_this.$router.push({
							path: '/'
						})
					} else {
						_this.$message({
							type: "error",
							message: '添加失败'
						})
					}
				})
			}
		}
}
</script>
<style>
	.shopcon{
		max-width: 800px;
		margin:30px auto;
	}
</style>


