<template>
    <div class="listwrap">
        <div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item>首页</el-breadcrumb-item>
		        <el-breadcrumb-item>客户列表</el-breadcrumb-item>
                <el-breadcrumb-item>数据监测</el-breadcrumb-item>
	      	</el-breadcrumb>
            <p>数据监测（场馆名称：{{ saasname }}，场馆ID: {{ saasid }}）时间区间：{{ showtime }}</p>
		</div>
        <div class="listbtn">
            <el-date-picker
                v-model="starttime"
                type="daterange"
                placeholder="选择日期范围"
                :picker-options="pickerDate"
                style="width: 220px">
            </el-date-picker>
			<el-button type="primary" icon="search" @click.native="search">查询</el-button>
		</div>
        <div class="listtable">
            <p><span class="tip"> 上课{{ coursetotal }}节，数据上传成功{{ uploadsuccess }}节，失败{{ uploadfail }}节</span></p>
            <el-table
                v-loading.body="loading"
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                prop="order"
                label="#">
                </el-table-column>
                <el-table-column
                prop="coursename"
                label="课程名称"
                width="180">
                </el-table-column>
                <el-table-column
                prop="deviceid"
                label="box序列号">
                </el-table-column>
                <el-table-column
                prop="starttime"
                label="上课时间">
                </el-table-column>
                <el-table-column
                prop="endtime"
                label="下课时间">
                </el-table-column>
                <el-table-column
                prop="upload"
                label="数据是否上传">
                </el-table-column>
                <el-table-column
                inline-template
                label="数据操作">
                <span>
                    <el-button @click="detail(row.heartlog,row.result)" type="primary" size="small">查看详情</el-button>
                </span>
                </el-table-column>
            </el-table>
            <pre>{{ resulttext }}</pre>
        </div>
    </div>
</template>
<script>
import config from '../model/config'
import utils from '../model/utils'
import format from 'vux/src/components/datetime/format'
export default {
    mounted() {
            var today = new Date();
            today.setHours(0);
            today.setMinutes(0);
            today.setSeconds(0);
            today.setMilliseconds(0);
            var endday = new Date();
            endday.setHours(23);
            endday.setMinutes(59);
            endday.setSeconds(59);
            endday.setMilliseconds(0);
            this.starttime = [today, endday];
            this.showTable(this.starttime[0], this.starttime[1]);
        },
        data() {
            return {
                showtime: '',
                resulttext: '',
                starttime: [],
                coursetotal: 0,
                uploadsuccess: 0,
                uploadfail: 0,
                saasid: this.$route.params.id,
                saasname: this.$route.params.name,
                tableData: [],
                loading: true,
                pickerDate: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                }
            }
        },
        methods: {
            search() {
                this.starttime[0].setHours(0);
                this.starttime[0].setMinutes(0);
                this.starttime[0].setSeconds(0);
                this.starttime[0].setMilliseconds(0);
                this.starttime[1].setHours(23);
                this.starttime[1].setMinutes(59);
                this.starttime[1].setSeconds(59);
                this.starttime[1].setMilliseconds(0);
                if (this.starttime[0] > this.starttime[1]) {
                    this.$message({
                        message: '开始时间不能晚于结束时间，请重新选择时间',
                        type: 'warning'
                    })
                    return;
                }
                // 

                this.showTable(this.starttime[0], this.starttime[1])
            },
            detail(heartlog, result) {
                JSON.stringify(heartlog, null, "\t")
                var a = {
                    result: result,
                    heartlog: heartlog
                };
                this.resulttext = JSON.stringify(a, null, "\t");
            },
            isEmptyObject(o) {
                for (var x in o) {
                    return false
                }
                return true
            },
            showTable(starttime, endtime) {
                this.showtime = format(starttime, "YYYY-MM-DD HH:mm:ss") + ' ~ ' + format(endtime, "YYYY-MM-DD HH:mm:ss");
                var o = this;

                var url = `${config.rest}/object/get.${o.saasid}.datamonitor_${starttime.getTime()}_${endtime.getTime()}`
                this.loading = true;
                this.tableData = [];
                this.resulttext = "";
                utils.load(url, function(r) {
                    o.loading = false;
                    if (r && r.saas.list) {
                        var list = r.saas.list;
                        o.coursetotal = o.uploadsuccess = o.uploadfail = 0;
                        o.coursetotal = list.length;
                        for (let i = 0; i < list.length; i++) {
                            var upload = '失败';
                            if (list[i].lessonstatus == 'end' && !o.isEmptyObject(list[i].heartlog)) {
                                upload = '成功';
                                o.uploadsuccess++;
                            }
                            o.tableData.push({
                                order: i + 1,
                                coursename: list[i].coursename,
                                deviceid: list[i].deviceid,
                                starttime: format(new Date(list[i].lessonstarttime), "YYYY-MM-DD HH:mm:ss"),
                                endtime: list[i].lessonendtime ? format(new Date(list[i].lessonendtime), "YYYY-MM-DD HH:mm:ss") : '',
                                upload: upload,
                                heartlog: list[i].heartlog,
                                result: list[i].upresult.result
                            });
                        }
                        o.uploadfail = o.coursetotal - o.uploadsuccess;
                    }
                })
            }
        }
}
</script>
<style>
    .listbtn .mark {
        margin: 0 5px;
    }
    .listbtn button {
        margin-left: 5px;
    }
    .listtable .tip {
        font-size: 15px;
        color: #4db3ff;
        border-bottom: 1px solid #4db3ff;
    }
    .el-message-box {
        -webkit-user-select: text;
    }
    .el-message-box__message>p{
        word-break: break-all;
    }
</style>


