<template>
	<div class="listwrap">
		<div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item>对象管理</el-breadcrumb-item>
		        <el-breadcrumb-item>搜索</el-breadcrumb-item>
	      	</el-breadcrumb>
		</div>
		<div class="search">
			<el-form :inline="true" class="demo-form-inline">
			  <el-form-item>
			    <el-input v-model="searchtext" placeholder="搜索内容"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-button type="primary" @click="onSubmit"><i class="el-icon-search"></i>查询</el-button>
			  </el-form-item>
			</el-form>			
		</div>
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span style="line-height: 36px;">对象管理</span>
		    <span v-show="btnshow">
		    	<el-button style="float: right;" type="primary" @click="manager">设置管理员</el-button>
          <el-switch style="float: right;margin-top:10px;margin-right:20px;" on-text="设置" off-text="取消" v-model="baozadmin"></el-switch>
		    </span>
		  </div>
		  <div class="text item">
		  	Root：{{root}}
		  </div>
		  <div class="text item">
		  	ID：{{id}}
		  </div>
		  <div class="text item">
		  	Name：{{name}}
		  </div>
		</el-card>
	</div>
</template>
<script>
import utils from '../model/utils'
import config from '../model/config'
  export default {
  	data() {
  			return {
  				searchtext: '',
  				id:'',
  				root:'',
  				name:'',
  				btnshow:false,
  				baozadmin:false,
  			}
  		},
  		methods: {
  			onSubmit() {
          if(this.searchtext.trim() == '') return
  				this.search()
  			},
  			search() {
  				var _this = this;
  				var url = config.rest + '/object/get.对象管理.search_' + _this.searchtext;
  				utils.load(url, function(r) {
  					if(r.objectmanage.info && r.objectmanage.info.base.root){
  						_this.id = r.objectmanage.info.id;
  						_this.root = r.objectmanage.info.base.root;
  						_this.name = r.objectmanage.info.group?r.objectmanage.info.group.name:'';
  						if(r.objectmanage.info.base.root === "user") {
  							_this.btnshow = true;
  							_this.name = r.objectmanage.info.user.rname;
  							_this.baozadmin = r.objectmanage.info.user.baozadmin == 0?false:true;
  						}else{
  							_this.btnshow = false
  						}
  					}else{
  						_this.id = '';
  						_this.root = '';
  						_this.name = '';
  						_this.btnshow = false;
  						_this.$alert('不存在你搜索的对象', '提示')
  					}
  				})
  			},
  			manager(){
  				var _this = this;
  				var url = config.xhr + '/' + _this.id + '/user';
  				utils.rpc(url,'markadmin',{baozadmin:_this.baozadmin},function(r){
					if (!r || r.error) {
						_this.$alert('出错了','提示')
					} else {
						if (r.result !== -1 && !r.result.error) {
							_this.$message({
								type:'success',
								message:'设置成功'
							})
						} else {
							_this.$alert('出错了','提示')
						}
					}
  				})
  			}
  		}

  }
</script>
<style scoped>
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  .clearfix:after {
      clear: both
  }

  .box-card {
    width: 580px;
    margin: 10px auto;
  }
  .search {
  	text-align: center;
  }
</style>


