<template>
	<div class="chart">
		<div id="chart"></div>
	</div>
</template>
<script>
import G2 from 'g2'
export default {
	mounted () {
		
	},
	props: ['chartdata'],
	data () {
		return {
			
		}
	},
	methods: {
		init () {
			var o = this
			var data = o.chartdata.cdata
			if (o.chart) {
				o.chart.destroy()
			}
			o.chart = new G2.Chart({
			  id: 'chart',
			  height: 360,
			  forceFit: true
			})
			var defs = {
				'time': {
			    	alias: o.chartdata.x,
			    	type: 'cat',
			    	mask: 'yyyy-mm-dd'
			  	},
			  	'use': {
			    	alias: o.chartdata.y + '（%）'
			  	}
			}
			o.chart.source(data, defs)
			o.chart.tooltip(true, {
			  title: null // 默认标题不显示
			})
			o.chart.line().position('time*use').size(2).color('#20a0ff')
			o.chart.render()
		}
	}
}
</script>
<style>
	
</style>


