import config from './config'
import md5 from './myMd5'
import utils from './utils'

var session = {};
var clientcache = {};

function sync(cb) {
	if (session && session.id) {
		cb({
			result: session.id
		});
		return;
	} else {
		session = {};
	}
	utils.rpc(config.session, "sync", 2, function(d) {
		if (d.result) {
			var f = 0,
				id, r = d.result;
			for (var k in r)
				if (session[k] != r[k]) {
					session[k] = r[k];
					if (k == "id") {
						id = r[k];
					}
				}
			cb({
				result: session.id
			});
		} else {
			if (cb)
				cb('sync failed.');
		}
	});
}

function signout(cb) {
	utils.rpc(config.session, "signout", 2, function(r) {
		session = {};
		cb(r);
	});
}

function get(id) {
	if (id != undefined)
		return session[id];
	else
		return session;
}

function signin(e, cb) {
	session = {}
	var name = e['name'];
	var password = e['password'];
	utils.rpc(config.session, "sync", 2, function(d) {
		var p = md5.hex_hmac(name, md5.hex(password));
		utils.rpc(config.xhr + '/0/user', 'signin', {
			"mail": name,
			"hpass": md5.hex_hmac(d.result.token, p)
		}, function(r) {
			if (r && r.result === 0) {
				sync(cb);
			} else {
				cb(r);
			}
		});
	});

}

module.exports = {
	client_set: function(id, value) {
		clientcache['client_' + id] = value;
	},
	client_get: function(id) {
		return clientcache['client_' + id];
	},
	get: get,
	sync: sync,
	signout: signout,
	signin: signin
};


