<style lang='scss' scoped>
.logo {
    float: left;
    margin: 0;
    font-size: 24px;
    line-height: 60px;
    color: #fff;
    font-weight: normal;
    a {
      text-decoration: none;
      color: #fff;
    }
}

.main-nav {
    float: left;
    margin-left: 50px;
    a {
        color: #fff;
        text-decoration: none;
    }
    .el-menu-item.is-active {
        a {
            color: #20a0ff;
        }
    }
}

.sub-nav {
    float: right;
}
.line {
  text-align: center;
  color: #ccc;
}
.router-link-active{
	color: #20a0ff;
}
</style>
<template>
<div class="header">
    <div class="grid">
        <h1 class="logo"><a><router-link to='/'>TAKE</router-link></a></h1>
        <el-menu theme="dark" default-active="/" router class="main-nav" mode="horizontal">
            <el-menu-item index="/">
                <i class="el-icon-menu"></i>商户列表
            </el-menu-item>
            <el-submenu index="2">
                <template slot="title"><i class="el-icon-menu"></i>对象管理</template>
                <el-menu-item index="/objmanage">对象管理</el-menu-item>
                <el-menu-item index="/powermanage">权限管理</el-menu-item>
            </el-submenu>
            <el-submenu index="1">
            <template slot="title">消息管理</template>
                <el-menu-item index="/weeksport">周报推送</el-menu-item>
                <el-menu-item index="/heartlogpush">运动结果推送</el-menu-item>
                <el-menu-item index="">预约推送</el-menu-item>
            </el-submenu>
            <el-menu-item index="/statistics">
                <i class="el-icon-menu"></i>数据统计
            </el-menu-item>
             <el-menu-item index="/sales">
                <i class="el-icon-menu"></i>销售活动
            </el-menu-item>
        </el-menu>
        <el-menu theme="dark" default-active="1" class="sub-nav" mode="horizontal">
            <el-submenu index="1">
                <template slot="title">退出</template>
                <el-menu-item index="1-1" @click.native="signout">退出</el-menu-item>
            </el-submenu>
        </el-menu>

    </div>
</div>

</template>
<script>
import session from '../model/session.js'
export default {
    data() {
            return {
                isadmin: session.get('isadmin'),
                changePassVisible: false,
                form: {
                    name: '',
                    newpass: '',
                    code: ''
                },
                sendmsg: '发送验证码',
                issend: false,
                timenum: '',
                subloading: false
            }
        },
        methods: {
            signout() {
                var _this = this;
                this.$confirm('确定要退出吗?', '提示', {
                    type: 'warning'
                }).then(
                    () => {
                        session.signout(function(r) {
                            if (r.result === 0) {
                                _this.$router.push({
                                    path: '/login'
                                })
                            }
                        })
                    }
                ).catch(
                    () => {
                        _this.$message({
                            type: 'info',
                            message: '取消退出!'
                        })
                    }
                )
            }
        }
}
</script>


