<template>
	<div class="listwrap">
		<div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item>销售活动</el-breadcrumb-item>
		        <el-breadcrumb-item>列表</el-breadcrumb-item>
	      	</el-breadcrumb>
		</div>
		<div class="listtable">
			<el-table
				border
				v-loading="loading"
				:data="tableData"
				style="width: 100%">
				<el-table-column
					prop="index"
					label="序号"
					width="100">
				</el-table-column>
				<el-table-column
					prop="workroom"
					label="场馆名称"
					>
				</el-table-column>
				<el-table-column
					prop="linkman"
					label="新客户名称"
					>
				</el-table-column>
				<el-table-column
					prop="sort"
					label="提交时间"
					width="200"
					>
				</el-table-column>
			</el-table>
		</div>
	</div>
</template>
<script>
	import utils from '../model/utils'
	import config from '../model/config'
	import format from 'vux/src/components/datetime/format'
	export default {
		mounted () {
			this.init('boxgourp')
		},
		data () {
			return {
				tableData: [],
				loading: true,
			}
		},
		methods: {
			init (list) {
				var o = this
				var url = `${config.rest}/object/get.对象管理.take_{list}`
				utils.load(url, function(r) {
					o.loading = false
					if (r && !r.error && r.objectmanage.list) {
						o.tableData = []
						var list = r.objectmanage.list;
						list = list.map(function (u,index) {
							u.index = index+1;
							u.sort = format(new Date(u.sort), "YYYY-MM-DD HH:mm:ss");
							return u;
						})
						o.tableData = list;
					} else {
						o.$message('出错啦')
					}
				})

			}
		}
	}
</script>
<style scoped>
	.el-form-item__content {
		margin-left: 70px !important;
	}
	.el-tag {
		padding: 0 20px;
		margin: 0 5px;
	}
</style>


