<template>
<div class="single-wrapper" v-loading.fullscreen="fullscreenLoading">
  <div class="login-container">
    <h1 class="title">TakePrj &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</h1>
    <el-form :inline="true" :model="formInline" @submit.prevent="onSubmit" class="align demo-form-inline">
      <el-form-item>
        <el-input v-on:keyup.enter.native="login" v-model="formInline.user" placeholder="手机号"></el-input>
      </el-form-item><el-form-item>
        <el-input type="password" v-on:keyup.enter.native="login" v-model="formInline.password" placeholder="密码"></el-input>
      </el-form-item><el-form-item>
        <el-button type="primary" @click.native="login">登陆</el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>
<script>
import session from '../model/session'

export default {
  data() {
      return {
        formInline: {
          user: '',
          password: ''
        },
        fullscreenLoading: false
      }
    },
    mounted() {
      var _this = this;
      _this.fullscreenLoading = true;
      session.sync(function(d) {
        if (d && d.result > 0) {
          if (session.get('baozadmin') !== 1) {
            _this.$alert('没用权限登录', '错误');
            _this.fullscreenLoading = false;
            return false;
          }
          _this.$router.push({
            path: ('/')
          });
        }
        _this.fullscreenLoading = false;
      });
    },
    methods: {
      login() {
        var _this = this;
        var user = this.formInline.user.trim()
        var pwd = this.formInline.password.trim()
        if (user == '' || pwd == '') {
          this.$alert('用户名或者密码不能为空', '错误'); //对输入进行判断
          return
        } else {
          if (!/^(13[0-9]|14[57]|15[0-9]|17[03678]|18[0-9])\d{8}$/.test(user)) {
            this.$alert('手机号码填写不正确', '错误');
            return;
          }
          this.fullscreenLoading = true;
          session.signin({
            name: user,
            password: pwd
          }, function(r) {
            console.log('baozadmin', session.get('baozadmin'))

            if (r && r.result > 0) {
              if (session.get('baozadmin') !== 1) {
                _this.$alert('没用权限登录', '错误');
                _this.fullscreenLoading = false;
                return false;
              }
              _this.$router.push({
                path: session.client_get('backurl') || '/'
              });
            } else {
              var msg = '出错了';
              if (r && r.result) {
                if (r.result.msg) msg = r.result.msg;
                if (r.result.code) msg = msg + '[' + r.result.code + ']';

                _this.$alert(msg, '错误');
              }
            }
            _this.fullscreenLoading = false;
          });
        };
      },
    }
};
</script>

<style lang="css" scoped>
.title {
  text-align: center;
  color: #fff;
  margin: 200px 0 100px;
}
.single-wrapper {
  height: 100%;
  background: #324057;
  overflow: hidden;
}
.login-container {
  width: 490px;
  margin: 100px auto;
}
.align{
  text-align: center;
}
</style>


