<template>
	<div class="listwrap">
		<div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
		        <el-breadcrumb-item>推送成功率数据监测</el-breadcrumb-item>
	      	</el-breadcrumb>
	      	<div class="tabs">
	      		<el-tabs @tab-click="handleClick">
				    <el-tab-pane label="盒子使用率" name="boxusage">
				    </el-tab-pane>
				    <el-tab-pane label="平均上课数" name="averclass">
				    </el-tab-pane>
				    <el-tab-pane label="人次" name="person">
					</el-tab-pane>
				    <el-tab-pane label="微信端绑定数" name="wxbind">
					</el-tab-pane>
				    <el-tab-pane label="微信端pv/uv" name="pvuv">
					</el-tab-pane>
		  		</el-tabs>
	      	</div>
		</div>
		<div class="alltable">
			<component :is="currentView"></component>
		</div>
	</div>
</template>
<script>
import config from '../model/config'
import utils from '../model/utils'
import boxusage from './boxusage' 
import averclass from './averclass'
import person from './person'
import wxbind from './wxbind'
import pvuv from './pvuv'
export default {
	mounted () {
		
	},
	data () {
		return {
			currentView: 'boxusage'
		}
	},
	methods: {
		handleClick (tab, event) {
			var view = tab.name
			this.currentView = view
		}
	},
	components: {
		boxusage,
		averclass,
		person,
		wxbind,
		pvuv
	}
}
</script>
<style>
	.tabs .el-tabs {
		width: 100%;
		padding-right: 30px;
	}
	.alltable {
		border: 1px solid #d3dce6;
		margin: 0 30px;
		padding: 20px;
	}
	.alltable table thead th {
		background-color: #20a0ff; 
	}
	.alltable table thead th .cell {
		background-color: #20a0ff; 
		color: #fff;
		font-weight: normal;
		text-align: center;
	}
	.alltable table thead th .cell .caret-wrapper .ascending{
		border-bottom: 5px solid #fff;
	}
	.alltable table thead th .cell .caret-wrapper .descending {
		border-top: 5px solid #fff;
	}
	.alltable table tbody tr {
		text-align: center;
	}
	.alltable p {
		color: #8492a6;
	}
 </style>


