<template>
	<div class="listwrap">
		<div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item>首页</el-breadcrumb-item>
		        <el-breadcrumb-item>客户列表</el-breadcrumb-item>
	      	</el-breadcrumb>
		</div>
		<div class="listbtn">
			<el-button type="primary" icon="plus" @click.native="addshop"> 添加商户</el-button>
		</div>
		<div class="listtable">
			  <el-table
			    :data="tableData"
			    border
			    loading="listloading"
			    style="width: 100%">
			    <el-table-column
			      property="phone"
			      label="手机"
			      width="130">
			    </el-table-column>
			   	<el-table-column
			      property="rname"
			      label="姓名"
			      width="100">
			    </el-table-column>
			    <el-table-column
			    property="id"
			    label="商户ID"
			    width="120">
			    </el-table-column>
			    <el-table-column
			      property="wname"
			      label="场馆名称">
			    </el-table-column>
			    <el-table-column
			      property="level"
			      sortable
			      label="类型">
			    </el-table-column>
			    <el-table-column
			      property="created"
			      sortable
			      label="开通日期">
			    </el-table-column>
			    <el-table-column
			      inline-template
			      label="操作">
			      <div class="listdo">
				       <a href="javascript::"><router-link :to="'/boxlist/'+row.id">详情</router-link></a>
							 <a href="javascript:;"><router-link :to="'/datamonitor/'+  row.id  + '/' + row.wname">数据监测</router-link></a>
			      </div>
			    </el-table-column>
			  </el-table>
		 </div>
	</div>
</template>
<script>
import config from '../model/config'
import utils from '../model/utils'
import format from 'vux/src/components/datetime/format'
export default {
	data() {
			return {
				tableData: [],
				dialogFormVisible: false,
				level: [, 'saas平台', '心率系统16', '心率系统36', 'saas+心率系统16', 'saas+心率系统36'],
				levelvalue: '',
				listloading: true
			}
		},
		mounted() {
			var _this = this;
			var url = config.rest + "/object/get." + '~健身工作室' + ".saas";
			utils.load(url, function(r) {
				var datalist = [];
				if (r && r.label.list) {
					_this.listloading = false;
					var d = r.label.list;
					for (var i in d) {
						if (d[i].data.createType && d[i].data.createType === 'human') datalist.push({
							phone: d[i].data.user.user.phone,
							id: d[i].id,
							rname: d[i].data.user.user.rname || "",
							wname: d[i].data.wname,
							level: _this.level[(d[i].data.level || 4)],
							created: format(new Date(d[i].data.created), "YYYY-MM-DD HH:mm:ss")
						});
					}
				}
				_this.tableData = datalist;
			})
		},
		methods: {
			addshop: function() {
				this.$router.push('/addshop')
			}
		}
}
</script>
<style>
.listfl{
	margin-top: 30px;
	float:right;
}
.listdo a{
	text-decoration: none;
	color:#20A0FF;
	margin: 0 3px;
}
</style>


