import Vue from 'vue'
import Router from 'vue-router'
import Vueresource from 'vue-resource'
import Element from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import session from '../model/session.js'
import Home from '../views/Home'
import Login from '../views/Login'
import Userlists from '../views/userlists'
import Boxlist from '../views/boxlist'
import Addshop from '../views/addshop'
import Datamonitor from '../views/datamonitor'
import objmanage from '../views/objmanage'
import weeksport from '../views/weeksport'
import heartlogpush from '../views/heartlogpush'
import powermanage from '../views/powermanage'
import statistics from '../views/statistics'
import sales from '../views/sales'
Vue.use(Vueresource)
Vue.use(Router)
Vue.use(Element)
export default new Router({
	mode: 'history',
	routes: [{
		path: '/',
		component: Home,
		beforeEnter: (to, from, next) => {
			if (session.get('baozadmin') === 1) {
				next()
			} else {
				next('/login')
			}
		},
		children: [{
			path: '',
			component: Userlists
		}, {
			path: '/objmanage',
			component: objmanage
		}, {
			path: '/boxlist/:id',
			component: Boxlist
		}, {
			path: '/datamonitor/:id/:name',
			component: Datamonitor
		}, {
			path: '/addshop',
			component: Addshop
		},{
			path: '/weeksport',
			component: weeksport
		},{
			path: '/heartlogpush',
			component: heartlogpush
		}, {
			path: '/powermanage',
			component: powermanage
		}, {
			path: '/statistics',
			component: statistics
		},{
			path: '/sales',
			component: sales
		}]
	}, {
		path: '/login',
		component: Login
	}]
})


