<template>
	<div class="condition">
		<div class="time">时间：</div>
		<div class="toggle">
			<ul>
				<li v-for="(tab,index) in tabs" :class="{active: active == index}" @click="toggle(index)">
					{{ tab.t }}
				</li>
			</ul>
		</div>
		<div class="datepick">
			<el-date-picker
		      v-model="week"
		      v-show = "active == 0"
		      type="week"
			  placeholder="选择周"
			  format="yyyy-MM-dd | 第 WW 周"
		      :picker-options="pickerOptions"
		      @change="update('week')">
			</el-date-picker>
			<el-date-picker
			    v-model="month"
			    v-show = "active == 1"
			    type="month"
			    placeholder="选择月"
			    @change="update('month')">
			</el-date-picker>
		</div>
	</div>
</template>
<script>
export default {
	name: 'datapick',
	mounted () {
		this.init()
	},
	data () {
		return {
			week: '',
			month: '',
			active: 0,
			tabs: [
				{
					t: '周',
					v: 'm'
				},{
					t: '月',
					v: 'd'
				}
			],
			pickerOptions: {
          		disabledDate (t) {
          			// 获取本周周一
          			var today = new Date()
          			var utime = today.getTime()
          			var day = today.getDay()
          			if (today.getDay() == 0) {
          				day = 6
          			}
          			var time = day * 1000 * 60 * 60 *24
          			var monday = utime - time
          			monday = new Date(monday).setHours(0,0,0,0)
            		return t.getTime() >= monday
          		}
        	}
		}
	},
	methods: {
		init () {
			var options = {
				duration: 'week',
				btime: this.getMonday() - 84 * 60 * 60 * 24 * 1000,
				etime: this.getMonday(),
				tablebtime: this.getMonday() - 7 * 60 * 60 * 24 * 1000
			}
			this.$emit('update-chart', options)
		},
		toggle (i) {
			this.active = i
			if (i == 1) {
				this.week = ''
				var f = new Date()
				f.setDate(1)
				var options = {
					duration: 'month',
					btime: new Date(f).setHours(0,0,0,0) - 365 * 60 * 60 * 24 * 1000,
					etime: new Date(f).setHours(0,0,0,0),
					tablebtime: new Date(f).setHours(0,0,0,0) - 30 * 60 * 60 * 24 * 1000
				}
				this.$emit('update-chart', options)
			} else {
				// this.month = ''
				this.init()
			}
		},
		update (type) {
			if (type == 'week') {
				var options = {
					duration: 'week',
					btime: new Date(this.week.split('|')[0]).setHours(0,0,0,0) - 84 * 60 * 60 * 24 * 1000,
					etime: new Date(this.week.split('|')[0]).setHours(0,0,0,0)
				}
			}
			if (type == 'month') {
				var options = {
					duration: 'month',
					btime: new Date(this.month).setHours(0,0,0,0) - 365 * 60 * 60 * 24 * 1000,
					etime: new Date(this.month).setHours(0,0,0,0)
				}
			}
			this.$emit('update-chart', options)
		},
		getMonday (t) {
			var today = t ? new Date(t) : new Date();
			var day = today.getDay() - 1; //传入的是周几
			var unixtime = today.getTime(); //传入时间戳
			if (today.getDay() == 0) day = 6;
			var time = day * 1000 * 60 * 60 * 24; //要减去的时间
			var monday = unixtime - time; //周一的时间
			monday = new Date(monday);
			monday.setHours(0, 0, 0, 0); //本周一的零点 
			return new Date(monday).getTime();
		}
	}
}
</script>


