<template>
	<div class="listwrap">
		<div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item>权限管理</el-breadcrumb-item>
		        <el-breadcrumb-item>列表</el-breadcrumb-item>
	      	</el-breadcrumb>
		</div>
		<div class="listbtn">
			<el-button type="primary" icon="plus" @click="dialogFormVisible = true">添加用户</el-button>
		</div>
		<div class="listtable">
			<el-table
				border
				v-loading="loading"
				:data="tableData"
				style="width: 100%">
				<el-table-column
					prop="name"
					label="姓名"
					width="280">
				</el-table-column>
				<el-table-column
					prop="roles"
					label="角色"
					width="380"
					inline-template
					>
					<el-tag 
						type="primary" 
						v-for="role in row.roles">
						{{ role }}
					</el-tag>
				</el-table-column>
				<el-table-column
					label="操作"
					inline-template>
					<span>
                    	<el-button @click="option(row.id, row.roles, 'delete')" type="primary" size="small">删除所有权限</el-button>
                    	<el-button @click="option(row.id, row.roles, 'update')" type="primary" size="small">修改权限</el-button>
                	</span>
				</el-table-column>
			</el-table>
		</div>
		<div class="adduser">
			<el-dialog title="添加用户" v-model="dialogFormVisible" size="tiny">
				<el-form>
					<el-form-item label="手机号码">
					    <el-input v-model="newnum" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="角色">
					    <el-checkbox-group v-model="checkList">
					        <el-checkbox label="root"></el-checkbox>
					        <el-checkbox label="产品"></el-checkbox>
					        <el-checkbox label="运营"></el-checkbox>
					    </el-checkbox-group>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
				  <el-button @click="dialogFormVisible = false">取 消</el-button>
				  <el-button type="primary" @click="adduser">确 定</el-button>
				</div>
			</el-dialog>	
		</div>
		<div class="deleteroles">
			<el-dialog title="提示" v-model="deletedialog" size="tiny">
			  <span>确定要删除该用户所有权限吗</span>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="deletedialog = false">取 消</el-button>
			    <el-button type="primary" @click="confirm('delete')">确 定</el-button>
			  </span>
			</el-dialog>
		</div>
		<div class="setroles">
			<el-dialog title="修改角色" v-model="updatedialog" size="tiny">
				<el-form>
					<el-form-item label="角色" >
					    <el-checkbox-group v-model="updatelist">
					        <el-checkbox label="root"></el-checkbox>
					        <el-checkbox label="产品"></el-checkbox>
					        <el-checkbox label="运营"></el-checkbox>
					    </el-checkbox-group>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
				  <el-button @click="updatedialog = false">取 消</el-button>
				  <el-button type="primary" @click="confirm('update')">确 定</el-button>
				</div>
			</el-dialog>	
		</div>
	</div>
</template>
<script>
	import utils from '../model/utils'
	import config from '../model/config'
	export default {
		mounted () {
			this.init()
		},
		data () {
			return {
				tableData: [],
				dialogFormVisible: false,
				deletedialog: false,
				updatedialog: false,
				newnum: '',
				checkList: [],
				loading: true,
				optionid: '',
				updatelist: []
			}
		},
		methods: {
			init () {
				var o = this
				var url = `${config.rest}/object/get.对象管理.list`
				utils.load(url, function(r) {
					o.loading = false
					if (r && !r.error && r.bind.list) {
						o.tableData = []
						r.bind.list.map(function (u) {
							var user = {
								id: u.user.user.id,
								name: u.user.user.name || u.user.user.phone,
								roles: u.roles
							}
							o.tableData.push(user)
						})
					} else {
						o.$message('出错啦')
					}
				})

			},
			adduser () {
				var o = this
				var info = {
					username: o.newnum.trim(),
					roles: o.checkList
				}
				o.checkList = []
				o.newnum = ''
				if(info.username == '') {
					o.$message('用户名不能为空')
					return
				}
				if(info.roles.length == 0) {
					o.$message('请至少选择一种角色')
					return
				}
				var url = config.rest + '/object/get.对象管理.search_' + info.username;
				utils.load(url, function (r) {
					if(r.objectmanage.info && r.objectmanage.info.base.root) {
						info.username = r.objectmanage.info.id
						utils.rpc(config.xhr + '/对象管理/bind', 'setroles', info, function (r) {
							if(r && !r.error) {
								o.init()
								o.dialogFormVisible = false
								this.$message({
								    message: '恭喜你，添加成功',
							        type: 'success'
						        })
							}
						})
					} else {
						o.$message('用户不存在')
						return
					}
				})
			},
			option (id, roles, type) {
				var o = this
				if(type == 'delete') {
					o.deletedialog = true
					o.optionid = id
					o.updatelist = []
				}else {
					o.updatedialog = true
					o.optionid = id
					o.updatelist = roles
				}
			},
			confirm (type) {
				var o = this
				var info = {
					username: o.optionid,
					roles: o.updatelist
				}
				utils.rpc(config.xhr + '/对象管理/bind', 'setroles', info, function (r) {
					if(r && !r.error) {
						o.init()
						if(type == 'delete') {
							o.deletedialog = false
							this.$message({
							    message: '删除成功',
						        type: 'success'
					        })
						}else {
							o.updatedialog = false
							this.$message({
							    message: '修改成功',
						        type: 'success'
					        })
						}
					}
				})
			}
		}
	}
</script>
<style scoped>
	.el-form-item__content {
		margin-left: 70px !important;
	}
	.el-tag {
		padding: 0 20px;
		margin: 0 5px;
	}
</style>


