<template>
	<div class="showdata">
		<div class="condition">
			<div class="time" style="border-bottom:1px solid #d3dce6">
				门店数量<span>{{ all }}</span>家，微信端绑定<span>{{ isopenwx }}</span>家，未绑定<span>{{ notopenwx }}</span>家
			</div>
		</div>
		<div class="select">
			<el-select v-model="mname" placeholder="请选择" @change="filter">
			    <el-option
			      v-for="item in bindwx"
			      :label="item.label"
			      :value="item.value">
			    </el-option>
			 </el-select>
		</div>
		<div class="result">
			<p>统计表</p>	
			<el-table
				v-loading.body="loading"
			    :data="tableData"
			    border
			    style="width:100%">
			    <el-table-column
			      type="index"
			      label="#"
			      width="180">
			    </el-table-column>
			    <el-table-column
			      prop="gname"
			      label="门店名称">
			    </el-table-column>
			    <el-table-column
			      prop="bindwx"
			      label="是否绑定微信"
			      width="200">
			    </el-table-column>
			    <el-table-column
			      prop="bindtime"
			      label="绑定时间">
			    </el-table-column>
			</el-table>
		</div>
	</div>
</template>
<script>
import config from '../model/config'
import utils from '../model/utils'
import format from 'vux/src/components/datetime/format'
export default {
	mounted () {
		this.init()
	},
	data () {
		return {
			tableData: [],
			bindData: [],
			notBinddata: [],
			allData: [],
			bindwx: [{
				value: 'all',
				label: '全部门店'
		        }, {
		          value: 'bindwx',
		          label: '微信端已绑定门店'
		        }, {
		          value: 'nbindwx',
		          label: '微信端未绑定门店'
		        }],
			mname: '',
			all: 0,
			isopenwx: 0,
			notopenwx: 0,
			loading: true
		}
	},
	methods: {
		init () {
			var o = this
			var url = `${config.rest}/object/get.~健身工作室.openwxlist`
			utils.load(url, function(r) {
				o.loading = false
				if (r && r.label.list) {
					r.label.list.map(function(c, index) {
						var i = {
							gname: c.data.wname,
							bindwx: c.data.isopenwx ? '是' : '否',
							bindtime: c.data.created ? format(new Date(c.data.created), "YYYY-MM-DD hh:mm:ss") : '' 
						}
						o.all++
						if(c.data.isopenwx) {
							o.isopenwx++
							o.bindData.push(i)
						}else {
							o.notopenwx++
							o.notBinddata.push(i)
						}
						o.allData.push(i)
					})
				} else {
					o.$message('出错啦')
				}
				o.tableData = o.allData
			})
		},
		filter () {
			var o = this
			switch (o.mname) {
				case 'all':
					o.tableData = o.allData
					break
				case 'bindwx':
					o.tableData = o.bindData
					break
				default:
					o.tableData = o.notBinddata
			}
		}
	}
}
</script>
<style>
	
</style>


