<template>
	<div class="showdata">
		<datapick @update-chart="boxinit"></datapick>
		<div class="result">
			<p>趋势图</p>
			<chart :chartdata="chartdata"></chart>
			<p>统计表</p>	
			<el-table
			    :data="tableData"
			    border
			    style="width: 100%">
			    <el-table-column
			      type="index"
			      label="#"
			      width="180">
			    </el-table-column>
			    <el-table-column
			      prop="gymName"
			      label="门店名称">
			    </el-table-column>
			    <el-table-column
			      prop="deviceid"
			      label="盒子序列号"
			      width="250">
			    </el-table-column>
			    <el-table-column
			      prop="date"
			      label="日期">
			    </el-table-column>
			    <el-table-column
			      prop="useNum"
			      sortable
			      width="180"
			      label="使用次数">
			    </el-table-column>
			</el-table>
		</div>
	</div>
</template>
<script>
import chart from './chart'
import datapick from './datapick'
import utils from '../model/utils'
import config from '../model/config'
import format from 'vux/src/components/datetime/format'
export default {
	mounted () {
	},
	data () {
		return {
			chartdata: {
				cdata: [],
				x: '',
				y: ''
			},
			tableData: []
		}
	},
	methods: {
		boxinit (options) {
			var o = this
			var chartoptions = {
				duration: options.duration,
				btime: options.btime,
				etime: options.etime
			}
			utils.rpc(config.xhr + '/统计后台/stat', 'getBoxUseRate', chartoptions, function (r) {
				if(r && r.result) {
					o.chartdata.cdata = []
					if (chartoptions.duration == 'month') {
						var btime = new Date(chartoptions.btime)
						var year = btime.getFullYear()
						var month = btime.getMonth() + 1
						for (var i = 0; i < 12; i++) {
							var cdata = {}
							cdata['time'] = `${year}年${month}月`
							cdata['use'] = 0
							month ++ 
							if(month > 12) {
								year ++ 
								month = 1
							}
							o.chartdata.cdata.push(cdata)
						}
						o.chartdata.x = '月份'	
						o.chartdata.y = '盒子使用率'	
						r.result.map(function(data) {
							var date = new Date(data.date)
							var year = date.getFullYear()
							var month = date.getMonth() + 1
							for (var i = 0; i < o.chartdata.cdata.length; i++) {
								if(`${year}年${month}月` == o.chartdata.cdata[i]['time']) {
									o.chartdata.cdata[i]['use'] = data.value
								}
							};
							
						})
						
					}
					if(chartoptions.duration == 'week') {
						var btime = new Date(chartoptions.btime)
						for (var i = 0; i < 12; i++) {
							var cdata = {}
							cdata['time'] = format(btime, "YYYY-MM-DD")
							cdata['use'] = 0			
							o.chartdata.cdata.push(cdata)
							btime = new Date(btime.getTime() + 7 * 60 * 60 * 24 * 1000)
						}
						o.chartdata.x = '周'	
						o.chartdata.y = '盒子使用率'	
						r.result.map(function(data) {
							var d = format(new Date(data.date), "YYYY-MM-DD")
							for (var i = 0; i < o.chartdata.cdata.length; i++) {
								if(d== o.chartdata.cdata[i]['time']) {
									o.chartdata.cdata[i]['use'] = data.value
								}
							};
						})	
					}
					o.$children[1].init()
				}
			})
			
			var tableoptions = {}
			if (!options.tablebtime) {
				if (options.duration == 'week') {
					tableoptions = {
						duration: 'week',
						btime: options.etime,
						etime: options.etime + 7 * 24 * 60 * 60 * 1000 - 1
					}
				}
				if (options.duration == 'month') {
					tableoptions = {
						duration: 'month',
						btime: options.etime,
						etime: options.etime + 30 * 24 * 60 * 60 * 1000 - 1 
					}
				}
			} else {
				if (options.duration == 'week') {
					tableoptions = {
						duration: 'week',
						btime: options.tablebtime,
						etime: options.etime - 1,
					}
				} else {
					tableoptions = {
						duration: 'month',
						btime: options.tablebtime,
						etime: options.etime - 1
					}
				}
			}
			o.tableData = []
			utils.rpc(config.xhr + '/统计后台/stat', 'getBoxUseDevice', tableoptions, function (r) {
				if (r && r.result && r.result.length > 0) {
					o.tableData = r.result
				}
			})
		}
	},
	components: {
		chart,
		datapick
	}
}
</script>
<style>
	.showdata .condition {
		display: flex;
		flex-direction: row;
		height: 35px;
	}
	.showdata .condition .time {
		height: 100%;
		flex: 1;
		line-height: 38px;
		color: #8492a6;
	}
	.showdata .condition .time span {
		padding: 5px;
	}
	.showdata .condition .toggle {
		height: 100%;
		flex: 4;
		line-height: 35px;
	}
	.showdata .condition .datepick {
		height: 100%;
		flex: 16;
		line-height: 35px;
		padding-left: 20px;
	}
	.showdata .condition .toggle ul{
		border: 1px solid #d3dce6;
		border-radius: 4px;
		height: 100%;
		margin: 0;
		padding: 0;
		list-style: none;
		clear: both;
	}
	.showdata .condition .toggle ul li {
		float: left;
		width: 50%;
		text-align: center;
		color: #8492a6;
	}
	.showdata .condition .toggle ul .active {
		background: #20a0ff !important;
		color: #fff !important; 
	}
	.showdata .result {
		margin-top: 40px;
	}
	.showdata .result p {
		color: #8492a6;
	}
	.showdata .result .class {
		text-align: center;
	}
	.showdata .result .class span {
		display: inline-block;
		padding: 3px 25px;
		border: 1px solid #d3dce6;
		border-radius: 4px;
		cursor: pointer;
	}
	.showdata .result .class .cactive {
		color: #fff;
		background: #20a0ff;
		border:none;
	}
	.showdata .result .class span:first-child {
		padding: 3px 34px;
	}
	.showdata .select {
		display: flex;
		flex-direction: row;
		width: 20%;
		margin-top: 20px;
	}
	.showdata .select input {
		color: #8492a6;
	}
	.showdata .select .el-select {
		flex: 1;
		margin-right: 10px;
	}
</style>


