<template>
	<div class="showdata">
		<datapick @update-chart="avginit"></datapick>
		<div class="result">
			<p>趋势图</p>
			<p class="class">
				<span @click="chooseop('o')" :class="{'cactive': classtype == 'o'}">团课</span>     <span @click="chooseop('p')" :class="{'cactive': classtype == 'p'}">私教课</span>
			</p>
			<chart :chartdata="chartdata"></chart>
			<p>统计表</p>	
			<el-table
			    :data="tableData"
			    border
			    style="width: 100%">
			    <el-table-column
			      type="index"
			      label="#"
			      width="180">
			    </el-table-column>
			    <el-table-column
			      prop="gymName"
			      label="门店名称">
			    </el-table-column>
			    <el-table-column
			      prop="deviceid"
			      label="盒子序列号"
			      width="200">
			    </el-table-column>
			    <el-table-column
			      prop="date"
			      label="日期">
			    </el-table-column>
			    <el-table-column
			      prop="classNum"
			      sortable
			      width="180"
			      label="上课总数">
			    </el-table-column>
			    <el-table-column
			      prop="openClassNum"
			      sortable
			      width="180"
			      label="团课数">
			    </el-table-column>
			    <el-table-column
			      prop="personClassNum"
			      sortable
			      width="180"
			      label="私教课数">
			    </el-table-column>
			</el-table>
		</div>
	</div>
</template>
<script>
import chart from './chart'
import datapick from './datapick'
import utils from '../model/utils'
import config from '../model/config'
import format from 'vux/src/components/datetime/format'
export default {
	mounted () {

	},
	data () {
		return {
			date: '',
			month: '',
			active: 0,
			classtype: '',
			tabs: [
				{
					t: '周',
					v: 'm'
				},{
					t: '月',
					v: 'd'
				}
			],
			chartdata: {
				cdata: [],
				x: '',
				y: ''
			},
			totaldata: [],
			opendata: [],
			persondata: [],
			tableData: []
		}
	},
	methods: {
		toggle (i) {
			this.active = i
		},
		chooseop (type) {
			if (type == 'o') {
				this.classtype = this.classtype == '' || this.classtype == 'p'  ? this.classtype = 'o' : ''
				if (this.classtype == '') {
					this.chartdata.cdata = []
					this.chartdata.cdata = this.opendata
				} else {
					this.chartdata.cdata = []
					this.chartdata.cdata = this.totaldata
				}
				this.$children[1].init()
			}
			if (type == 'p') {
				this.classtype = this.classtype == '' || this.classtype == 'o' ? this.classtype = 'p' : ''
				if (this.classtype == '') {
					this.chartdata.cdata = []
					this.chartdata.cdata = this.persondata
				} else {
					this.chartdata.cdata = []
					this.chartdata.cdata = this.totaldata
				}
				this.$children[1].init()
			}
		},
		avginit (options) {
			var o = this
			var chartoptions = {
				duration: options.duration,
				btime: options.btime,
				etime: options.etime
			}
			utils.rpc(config.xhr + '/统计后台/stat', 'getBoxAverageClassNum', chartoptions, function (r) {
				if(r && r.result) {
					o.chartdata.cdata = []
					o.totaldata = []
					o.opendata = []
					o.persondata = []
					if (chartoptions.duration == 'month') {
						var btime = new Date(chartoptions.btime)
						var year = btime.getFullYear()
						var month = btime.getMonth() + 1
						for (var i = 0; i < 12; i++) {
							var cdata = {}
							cdata['time'] = `${year}年${month}月`
							cdata['use'] = 0
							month ++ 
							if(month > 12) {
								year ++ 
								month = 1
							}
							o.totaldata.push(cdata)
							o.persondata.push(cdata)
							o.opendata.push(cdata)
						}
						o.chartdata.x = 'time'	
						o.chartdata.y = 'use'	
						for ( var key in r.result) {
							var date = new Date(key)
							var year = date.getFullYear()
							var month = date.getMonth() + 1
							for (var i = 0; i < o.totaldata.length; i++) {
								console.log(o.totaldata[i]['time'])
								if(`${year}年${month}月` == o.totaldata[i]['time']) {
									o.totaldata[i]['use'] = r.result[key].classNumAverage
									o.opendata[i]['use'] = r.result[key].openClassNumAverage
									o.persondata[i]['use'] = r.result[key].personClassNumAverage
								}
							}
						}
						o.chartdata.cdata = o.totaldata
						o.$children[1].init()
					}
					if (chartoptions.duration == 'week') {
						var btime = new Date(chartoptions.btime)
						for (var i = 0; i < 12; i++) {
							var cdata = {}	
							cdata['time'] = format(btime, "YYYY-MM-DD")
							cdata['use'] = 0			
							o.totaldata.push(cdata)
							o.persondata.push(cdata)
							o.opendata.push(cdata)
							btime = new Date(btime.getTime() + 7 * 60 * 60 * 24 * 1000)
						}
						o.chartdata.x = '周'	
						o.chartdata.y = '平均上课数'	
						for ( var key in r.result) {
							var d = format(new Date(key), "YYYY-MM-DD")
							for (var i = 0; i < o.totaldata.length; i++) {
								if(d == o.totaldata[i]['time']) {
									o.totaldata[i]['use'] = r.result[key].classNumAverage
									o.opendata[i]['use'] = r.result[key].openClassNumAverage
									o.persondata[i]['use'] = r.result[key].personClassNumAverage
								}
							}
						}
						o.chartdata.cdata = o.totaldata
						o.$children[1].init()
					}

					
				}
			})
			var tableoptions = {}
			if (!options.tablebtime) {
				if (options.duration == 'week') {
					tableoptions = {
						duration: 'week',
						btime: options.etime,
						etime: options.etime + 7 * 24 * 60 * 60 * 1000 - 1
					}
				}
				if (options.duration == 'month') {
					tableoptions = {
						duration: 'month',
						btime: options.etime,
						etime: options.etime + 30 * 24 * 60 * 60 * 1000 - 1 
					}
				}
			} else {
				if (options.duration == 'week') {
					tableoptions = {
						duration: 'week',
						btime: options.tablebtime,
						etime: options.etime - 1,
					}
				} else {
					tableoptions = {
						duration: 'month',
						btime: options.tablebtime,
						etime: options.etime - 1
					}
				}
			}
			o.tableData = []
			utils.rpc(config.xhr + '/统计后台/stat', 'getBoxAverageClassNumDevice', tableoptions, function (r) {
				if (r && r.result && r.result.length > 0) {
					o.tableData = r.result
				}
			})
		}
	},
	components: {
		chart,
		datapick
	}
}
</script>
<style>
	
</style>


