<template>
	<div class="boxlist">
		<div class="listheader">
			<el-breadcrumb separator="/">
		        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
		        <el-breadcrumb-item :to="{ path: '/' }">客户列表</el-breadcrumb-item>
		        <el-breadcrumb-item>盒子列表</el-breadcrumb-item>
	      	</el-breadcrumb>
		</div>
		<h4 class="marl">商户信息</h4>
		<div class="listtable" v-loading="shop_load">
			  <el-table
			    :data="shopData"
			    border
			    style="width: 100%">
			    <el-table-column
			      property="phone"
			      label="手机"
			      width="180">
			    </el-table-column>
			    <el-table-column
			      property="name"
			      label="姓名"
			      width="0">
			    </el-table-column>
			    <el-table-column
			      property="shopname"
			      label="场馆名称">
			    </el-table-column>
			    <el-table-column
			      property="shoptype"
			      label="类型">
			    </el-table-column>
			    <el-table-column
			      property="starttime"
			      label="开通日期">
			    </el-table-column>
			    <el-table-column
			      property="endtime"
			      label="到期日期">
			    </el-table-column>
			    <el-table-column
			      property="msg"
			      label="剩余短信">
			    </el-table-column>
			  </el-table>

		 </div>
		 <h4 class="marl">盒子列表</h4>
		<div class="listtable" v-loading="box_load">
			  <el-table
			    :data="tableData"
			    border
			    style="width: 100%">
			    <el-table-column
			      property="boxid"
			      label="实体序列号">
			    </el-table-column>
			    <el-table-column
			      property="boxname"
			      label="名称"
			      >
			    </el-table-column>
			    <el-table-column
			      property="time"
			      label="添加时间"
			      >
			    </el-table-column>
			    <el-table-column
			      inline-template
			      label="操作">
			      <div>
			      	<span class="btnmar"><el-button :plain="true" type="info" size="small" @click.native="change(row.boxid,row.boxname)">修改</el-button></span>
			      	<span class="btnmar"><el-button :plain="true" type="danger" size="small" @click.native="deletebox(row.boxid)">删除</el-button></span>
			      </div>
			    </el-table-column>
			  </el-table>
		</div>
		<div class="boxbtn">
			<el-button type="primary" icon="plus" @click.native="addbox"> 添加盒子</el-button>
		</div>
	        <el-dialog size="tiny" title="添加盒子" v-model="dialogFormVisible">
	            <el-form>
	                <el-form-item label="实体序列号" label-width="100px">
	                    <el-input v-model="adddata.boxid" auto-complete="off"></el-input>
	                </el-form-item>
	                <el-form-item label="名称" label-width="100px">
	                    <el-input v-model="adddata.boxname" auto-complete="off"></el-input>
	                </el-form-item>
	            </el-form>
	            <span slot="footer" class="dialog-footer">
	         	<el-button @click.native="dialogFormVisible = false">取 消</el-button>
	          	<el-button type="primary" @click.native="addNewbox">确 定</el-button>
	        	</span>
	        </el-dialog>
	        <el-dialog size="tiny" title="修改" v-model="changeFormVisible">
	            <el-form>
	                <el-form-item label="实体序列号" label-width="100px">
	                    <el-input readonly="readonly" v-model="changedata.boxid" auto-complete="off"></el-input>
	                </el-form-item>
	                <el-form-item label="名称" label-width="100px">
	                    <el-input v-model="changedata.boxname" auto-complete="off"></el-input>
	                </el-form-item>
	            </el-form>
	            <span slot="footer" class="dialog-footer">
	         	<el-button @click.native="changeFormVisible = false">取 消</el-button>
	          	<el-button type="primary" @click.native="upDate">确 定</el-button>
	        	</span>
	        </el-dialog>
	</div>
</template>
<script>
import utils from '../model/utils'
import config from '../model/config'
import format from 'vux/src/components/datetime/format'
export default {
	data() {
			return {
				tableData: [],
				dialogFormVisible: false,
				adddata: {
					boxname: '',
					boxid: ''
				},
				shopData: [{
					phone: '',
					name: '',
					shopname: '',
					shoptype: '',
					starttime: '--',
					endtime: '--',
					msg: '--'
				}],
				changedata: {
					boxname: '',
					boxid: '',
				},
				id: '',
				changeFormVisible: false,
				level: [, 'saas平台', '心率系统16', '心率系统32', 'saas+心率系统16', 'saas+心率系统32'],
				shop_load: true,
				box_load: true

			}
		},
		mounted: function() {
			var _this = this;
			_this.init()
			_this.initsaas()
		},
		methods: {
			addbox: function() {
				this.dialogFormVisible = true;
			},
			addNewbox: function() {
				var _this = this;
				if (_this.adddata.boxname.trim() == '' || _this.adddata.boxid.trim() == '') {
					_this.$notify({
						title: '错误',
						message: '序列号或名称不能为空！',
						type: 'error'
					})
					return
				}
				var url = config.xhr + "/" + _this.id + "/saas";
				utils.rpc(url, "addheartbox", _this.adddata, function(r) {
					if (r && r.result === 0) {
						_this.init()
						_this.dialogFormVisible = false;
						_this.$notify({
							title: '成功',
							message: '添加成功',
							type: 'success'
						})
					} else if (r && r.result && r.result.error) {
						_this.$notify({
							title: '错误',
							message: '请输入正确序列号',
							type: 'error'
						})
					} else {
						_this.$notify({
							title: '错误',
							message: '出错了',
							type: 'error'
						})
					}
				})
			},
			change: function(id, name) {
				var _this = this;
				_this.changedata.boxname = name;
				_this.changedata.boxid = id;
				_this.changeFormVisible = true;
			},
			upDate: function() {
				var _this = this;
				if (_this.changedata.boxname == '') {
					_this.$alert('名字不能为空', '错误');
					return;
				}
				utils.rpc(config.xhr + "/" + _this.id + "/saas", "updateheartbox", _this.changedata, function(r) {
					if (r && r.result === 0) {
						_this.changeFormVisible = false;
						for (var i = 0, j = _this.tableData.length, k = _this.tableData; i < j; i++) {
							if (k[i].boxid === _this.changedata.boxid) {
								k[i].boxname = _this.changedata.boxname
								break;
							}
						}
						_this.$message({
							type: 'success',
							message: '修改成功'
						})
					} else {
						_this.changeFormVisible = false;
						_this.$alert('出错了', '提示')
					}
				})
			},
			deletebox: function(x) {
				var _this = this;
				var box = {
					boxid: x
				}
				this.$confirm('确定要删除吗?', '提示', {
					type: 'warning'
				}).then(
					function() {
						utils.rpc(config.xhr + "/" + _this.id + "/saas", "removeheartbox", box, function(r) {
							if (r && r.result === 0) {
								_this.$message({
									type: 'success',
									message: '删除成功'
								})
								for (var i = 0, j = _this.tableData.length, k = _this.tableData; i < j; i++) {
									if (k[i].boxid === x) {
										k.splice(i, 1);
										return;
									}
								}
							} else {
								_this.$alert('删除失败', '提示')
							}
						})
					}
				).catch(function() {
					_this.$message({
						type: 'info',
						message: '取消删除'
					})
				})
			},
			init: function() {
				var _this = this;
				_this.box_load = true
				_this.id = _this.$route.params.id;
				var url = config.rest + "/object/get." + _this.id + ".heartbox"
				utils.load(url, function(r) {
					if (r && !r.error && r.saas.list) {
						_this.box_load = false
						for (var i = 0, j = r.saas.list.length, k = r.saas.list; i < j; i++) {
							k[i].time = format(new Date(k[i].time), "YYYY-MM-DD hh:mm:ss")
						}
						_this.tableData = r.saas.list
					} else {

						_this.box_load = false;
					}
				})
			},
			initsaas: function() {
				var _this = this;
				_this.shop_load = true;
				utils.load(config.rest + "/object/get." + _this.id + ".saas", function(r) {
					if (r && r.saas) {
						_this.shop_load = false;
						_this.shopData[0].phone = r.saas.user.user.phone;
						_this.shopData[0].name = r.saas.user.user.rname;
						_this.shopData[0].shopname = r.saas.wname;
						_this.shopData[0].shoptype = _this.level[r.saas.level];
						_this.shopData[0].starttime = format(new Date(r.saas.user.user.jointime), "YYYY-MM-DD hh:mm:ss");
					} else {
						_this.shop_load = false;
						_this.$alert('出错了', '提示')
					}
				})
			}
		},
}
</script>
<style>
	.btnmar{
		margin-right: 20px;
	}
	.boxbtn{
		float:right;
		margin:0 30px;
	}
	.marl{
		margin-left: 30px;
	}
</style>


